import React from 'react';
import Slider from 'react-slick';
import VideoPlayer from '../VideoPlayer/VideoPlayer';
import Aux from '../Aux/Aux';
// import Slide from '../Slide/Slide';
// import PhotoArray from '../../photo-imports';

// CSS
import './Carousel.css';

// media
// import image01 from '../../images/yc_w_camera-1930.jpg';
// import wedding01 from '../../images/wedding-hand-tinted-02.jpg';
// import wedding02 from '../../images/wedding.jpg';
